@import "@/styles/colors.scss"; @import "@/styles/responsive.scss";























.saved-indicator {
  &__text {
    color: $white;
    margin: 0.25rem 1.25rem 0 0;
    opacity: 0;
    text-align: right;
    transition: all 0.2s ease-in-out;
    transform: translateX(1.25rem);

    &--saved {
      opacity: 1;
      transform: translateX(0);
    }
  }
}
