@import "@/styles/colors.scss"; @import "@/styles/responsive.scss";





















































.todos-add {
  align-items: center;
  background-color: $dark-blue;
  border-top-left-radius: 1.25rem;
  border-top-right-radius: 1.25rem;
  bottom: 0;
  display: flex;
  left: 0;
  height: 10rem;
  justify-content: center;
  position: fixed;
  right: 0;
  width: 100%;
  z-index: 9999;

  &__close {
    background-color: $dark-blue;
    padding: 0.75rem;
    border-radius: 50%;
    position: absolute;
    left: 50%;
    margin-left: -0.75rem;
    top: -0.75rem;

    &:hover {
      cursor: pointer;
    }

    svg {
      fill: $white;
      height: 1.5rem;
      width: 1.5rem;
    }
  }

  &__button {
    margin-left: 0.75rem;
  }

  &__controls {
    display: flex;
  }
}
