@import "@/styles/colors.scss"; @import "@/styles/responsive.scss";



















































































































































.todos {
  display: flex;
  flex-direction: column;
  padding-bottom: 3rem;

  &__header {
    align-items: baseline;
    display: flex;
    justify-content: space-between;
    padding: 2rem 0 1.25rem 0;
  }

  &__loading {
    align-items: center;
    color: $white;
    display: flex;
    flex-direction: column;
    font-style: italic;
    justify-content: center;
    margin-bottom: 1.75rem;

    svg {
      margin-top: 0.75rem;
      width: 2.5rem;
    }
  }

  &__title {
    color: $white;
    margin: 0 0 0.75rem;
  }
}

.todos-container,
.todos-empty {
  background-color: $white;
  border: 2px solid $blue;
  border-radius: 1.25rem;
  margin-bottom: 1.75rem;
  overflow: hidden;
}

.todos-empty {
  padding: 1.25rem;
}

.slide-enter-active {
  transition: all 0.3s ease-out;
}
.slide-leave-active {
  transition: all 0.3s ease-in;
}

.slide-enter,
.slide-leave-to {
  transform: translateY(100%);
}
