@import "@/styles/colors.scss"; @import "@/styles/responsive.scss";



















$size: 4.75rem;

button {
  align-self: flex-end;
  background-color: darken($blue, 15%);
  border-radius: 50%;
  box-shadow: 0px 5px 10px rgba($dark-blue, 0.5);
  border: none;
  bottom: 2rem;
  color: $white;
  height: $size;
  font-weight: bold;
  margin-right: 0;
  padding: 0.75rem 1rem;
  position: fixed;
  transition: all 0.3s ease-in;
  width: $size;
  z-index: 999;

  @include wider($mobile-landscape) {
    margin-right: -0.5rem;
  }

  &:disabled {
    opacity: 0.7;
  }

  &:hover {
    box-shadow: none;
  }
}
